<template>
  <div class="sortMenu">
    
      <ul class="menuList">
      	<li v-for="(item,index) in sorts" class="menuItem" @click="getSaleGoods(index)">
      		{{item}}
      	</li>
      </ul>
    
  </div>
</template>

<script>
import { bus } from '../../assets/bus.js'
export default {
  data(){
  	return{
  		goodsUrl:"",
  		sorts:[
  			"所有分类",
  			"代步工具",
  			"手机",
  			"电脑",
  			"数码",
  			"电器",
  			"衣物",
  			"书籍",
  			"体育健身",
  			"乐器",
        "其他"
  		],

  	}
  },
  methods:{
  	getSaleGoods:function(index){
  		var vm=this;
  		var data={
  			sort:index
  		}
      this.$router.push('/sale?type='+index);
      console.log("菜单发送了消息")
      bus.$emit("isGetGoodList",index);  			
  	}
  }
}
</script>

<style scoped>
  .sortMenu{
  	width: 100%;
  	height: 100%;
  	max-width: 200px;
  	background: #2b333b;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.5);
    
  }
  .menuList{
  	display: inline-block;
  	width: 100%;
  	height: 100%;
  	margin: 0;
  	padding: 0;
  	
  }
  .menuItem{
  	height: 20px;
  	padding: 15%;
  	vertical-align: bottom;
  	list-style: none;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    color: rgba(255,255,255,0.6);
    font-size: 20px;
    cursor: pointer;
	}
  .menuItem:hover{
    background: rgba(255,255,255,0.6);
    color: #fff;
    transition:1s all;
  }
  .menuItem:visited{
    background: rgba(0,0,0,0.2);
  }

  
</style>


