<template>
  <div class="releaseMenu">
    <div>
      <button @click="redirect()">发布二手</button>
    </div>
    <div>
      <button>求购二手</button>
    </div> 
  </div>
</template>

<script>
export default {
  data(){
    return {
      loginUrl:"/dachuang/isStatus"
    }
  },
  methods:{
    redirect:function(){
      
        this.$router.push("/sale/releaseSale");
   
    },
    checkLogin:function(){
      var vm=this;
      vm.$http.get(vm.loginUrl
              ).then(function(res){
                if(res.body.code==0){
                  console.log("用户未登录");
                  localStorage.user="";
                  this.$router.push("/login");
                }
              },function(err){
                console.log(err);
                this.$router.push("/login");
              })
    },
     
  }
}
</script>

<style scoped>
  .releaseMenu{
    width: 100px;
    text-align: center;
  }
  .releaseMenu button{
    width: 80px;
    height: 90px;
    margin: 20px;
    border-radius: 5px;
    color: #fff;
    font-size: 25px;
    font-weight: 700;
    background:rgba(253, 208, 72, 0.847058823529412);
    border: none;
  }
  .releaseMenu button:hover{
    cursor: pointer;
    background:rgba(253, 208, 72, 0.5);
  }

  
</style>


