<template>
  <div class="sale">
    <div class="sortMenuWrapper">
    <sortMenu></sortMenu>
    </div>
    <topMenu></topMenu>
    <transition name="fade">
    <router-view></router-view>
    </transition>
    <div class="releaseMenuWrapper">
    <releaseMenu></releaseMenu>
    </div>
  </div>
</template>

<script>
import sortMenu from '../sortMenu/sortMenu.vue';
import topMenu from '../topMenu/topMenu.vue';
import releaseMenu from '../releaseMenu/releaseMenu.vue';
export default {
  data(){
    return {

    }
  },
  components:{
    "sortMenu":sortMenu,
    "releaseMenu":releaseMenu,
    "topMenu":topMenu
  }
}
</script>

<style scoped>
  .sale{
    width: 100%;
    height: 100%;
    min-height: 1200px;
    position: relative;
  }
  .sortMenuWrapper{
  
    left: 0px;
    top: 0px;
    display: inline-block;
    position: fixed;
    height: 100%;
    width: 10%;
  }
  .releaseMenuWrapper{
    position: fixed;
    right: 40px;
    top: 30%;
  }
</style>


