<template>
  <div class="topMenu">
    
    <ul class="menuList">
      <li>
        <a href="/">首页</a>
      </li>
      <li>
        <a href="#/sale">二手商品</a>
      </li>
      <li>
        <a href="#/buy">求购商品</a>
      </li>
      <li>
        <a>关于我们</a>
      </li>
    </ul>
    <div class="user">
      <a id="avatar"  v-show="user">
        <img></img>
        <span>{{user}}</span>
      </a>
      <a v-show="user">个人中心</a>
      <a href="#/login" v-show="!user">登录</a>
      <a v-show="user" @click="log">退出登录</a>
      <a href="#/login" v-show="!user">注册</a>
    </div>
  </div>
</template>

<script>
import { bus } from '../../assets/bus.js'
export default {
  created(){
    var vm=this;
    this.user=localStorage.user;
    console.log(localStorage.user);
  },
  data(){
    return{
      logUrl:"/dachuang/LogServlet",
      user:""
    }
  },
  methods:{  
    
    log:function(){
      var vm=this;
      var model=this.registerModel;
    
      vm.$http.get(vm.logUrl
                    ).then(function(res){
                         console.log(res.body);
                      if(res.body.code==1){
                          vm.message="注销成功";
                          //vm.isLogin=false;
                          //vm.user.isLogin=false;
                          console.log(res.body);
                          bus.$emit("logSuccess");
                          vm.user="";
                          localStorage.user="";
                          console.log("用户注销成功")
                          this.$router.push("/sale"); 
                      }else{
                          vm.msg=res.body.message;
                        }
                      })
       

    },
    delCookie:function(name){
      console.log(this.getCookie(name))
      this.setCookie(name,"",-1);
    },
    getCookie:function(cname) {  
    var name = cname + "=";  
    var ca = document.cookie.split(';');  
    for(var i=0; i<ca.length; i++) {  
        var c = ca[i];  
        while (c.charAt(0)==' ') c = c.substring(1);  
        if (c.indexOf(name) != -1) return c.substring(name.length, c.length);  
    }   
    },
    setCookie:function(cname,cval,exdays){
      var exp=new Date();
      exp.setTime(exp.getTime()+(exdays*24*60*60*1000));

      var expires="expires="+exp.toUTCString();
      console.log(expires);
      document.cookie=cname+"="+cval+"; "+expires+"; path=/";
      console.log(document.cookie)
    }
  }
  
}
</script>

<style scoped>
  a{
    text-decoration: none;
    color: #000;
  }
  a:hover{
    cursor: pointer;
  }
  .menuList{
    flex:2;
    width: 65%;
    display: inline-block;
    margin: 0;
    padding-left: 20px ;   
   }
    
  .menuList li{
    height: 80px;
    line-height: 80px;
    padding: 0 20px;
    display: inline-block;
  }
  .topMenu{
    display: flex;
    flex-direction:column;
    height: 80px;
    width: 100%;
    line-height: 80px;
    display: inline-block;
    text-align: left;
    box-shadow: 0 4px 8px 0 rgba(7,17,27,0.1);
    font-size: 20px;
    font-weight: 600;
  }
 
  .user{
    flex:1;
    display: inline-block;
  }
  .user a{
    display: inline-block;
    font-size: 16px;
    padding: 0 10px;
    color: rgba(0,0,0,0.5);

  }
  #avatar{
    
    display: inline-block;
    height: 80px;
    line-height: 80px;
    border: none;
    border-radius: 50%;
  }
  #avatar img{
    width: 50px;
    height: 50px;
    vertical-align: middle;
    display: inline-block;
    background: rgba(0,0,0,0.5);
    border-radius: 50%;
    border: none;
  }
  #avatar span{
    display: inline-block;
  }
</style>


